.browser-card-container{
    display: grid;
    grid-template-columns: 2fr 1fr 9fr 2fr;
    padding: 20px 0;
}

.history-card-time{
    font-weight: 600;
    color: #475569;
    display: flex;
    align-items: center;
    justify-content: center;
}
.history-card-logo{
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 10px;
}

.history-card-details{
    display: flex;
    align-items: center;
    font-weight: 600;
    color: #475569;
    flex-wrap: wrap;
}

.history-card-domain{
    font-weight: 400;
    margin-left: 10px;
}

.history-card-logologo{
    width: 33px;
}
.history-delete-button{
    margin: auto;
    border: 0;
    background-color: white;
}